<style scoped>
    .security_item {
        display: flex;
        justify-content: space-between;
        padding: 10px 5px;
        border-bottom: 1px solid #ccc; 
    }
    .security_item .info .icon {
        float: left;
    }
    .security_item .info .icon img {
        width: 50px;
        height: 50px;
    }
    .security_item .info .title {
        display: block;
        margin-left: 60px;
        font-size: 16px;
        color: #333;
    }
    .security_item .info .description {
        display: block;
        margin-left: 60px;
        font-size: 14px;
        color: #ccc;
    }
    .security_item .operat {
        font-size: 14px;
        line-height: 40px;
    }
</style>
<template>
    <div>
        <Tabs type="card">
            <TabPane label="安全设置">
                <div class="security_item">
                    <div class="info">
                        <span class="icon">
                            <img src="../../resource/images/changePassword.png" alt="" />
                        </span>
                        <span class="title">
                            修改密码
                        </span>
                        <span class="description">
                            高复杂度密码可以使帐号更安全。推荐设置大于8位且包含大小写字母、数字及特殊字符的密码。
                        </span>
                    </div>
                    <div class="operat">
                        <a href="javascript:;" @click="changePassword = true">设置</a>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Modal
            v-model="changePassword"
            title="修改密码"
            @on-ok="ok"
            @on-cancel="cancel">
            <Input v-model="oldPassword" placeholder="原密码" style="margin: 10px 0;" />
            <Input v-model="newPassword" placeholder="新密码" style="margin: 10px 0;" />
            <Input v-model="rePassword" placeholder="确认密码" style="margin: 10px 0;" />
        </Modal>
    </div>
    
</template>
<script>
    import axios from "axios";
    import { BASE_URL, PROTOCOL } from "../../config/index.js";
    export default {
        data () {
            return {
                changePassword: false,
                oldPassword: "",
                newPassword: "",
                rePassword: "",
            }
        },
        methods: {
            ok () {
                if(this.newPassword === this.rePassword) {
                    axios.post(PROTOCOL+"//"+BASE_URL+"/auth/resetPassword/alterPassword",{
                        old_password: this.oldPassword,
                        password: this.newPassword,
                        password_confirmation: this.rePassword,
                    }).then(res => {
                        if(res.data.code == 1) {
                            alert(res.data.msg);
                        } else {
                            alert(res.data.msg);
                        }
                    });
                } else {
                    alert("两次密码不匹配");
                    setTimeout(() => {
                        this.changePassword = true;
                    }, 500);
                }
            },
            cancel () {
                // this.$Message.info('Clicked cancel');
            }
        }
    }
</script>